import { Component, Children } from 'react'
import PropTypes from 'prop-types'

let didWarnAboutReceivingStore = false
function warnAboutReceivingStore() {
  if (didWarnAboutReceivingStore) {
    return
  }
  didWarnAboutReceivingStore = true
}

export function createHistroy(hisKey = 'his', subKey) {
    const subscriptionKey = subKey || `${hisKey}Subscription`

    class History extends Component {
        getChildContext() {
          return { [hisKey]: this[hisKey], [subscriptionKey]: null }
        }

        constructor(props, context) {
          super(props, context)
          this[hisKey] = props.store;
        }

        render() {
          return Children.only(this.props.children)
        }
    }

    if (process.env.NODE_ENV !== 'production') {
        History.prototype.componentWillReceiveProps = function (nextProps) {
        if (this[hisKey] !== nextProps.store) {
          warnAboutReceivingStore()
        }
      }
    }

    History.propTypes = {
        children: PropTypes.element.isRequired,
    }
    History.childContextTypes = {
        
    }

    return History
}

export default createHistroy()



